<template>
	<view class="container">
		<!-- 分类导航栏 -->
		<scroll-view scroll-x="true" class="category-scroll" :scroll-with-animation="true">
			<view class="category-list">
				<!-- 单独处理"全部"项 -->
				<view class="category-item" :class="{ active: currentType === 'all' }" @click="changeType('all')">
					<text>全部</text>
					<text class="count">({{ typeCount.all || 0 }})</text>
				</view>

				<!-- 循环其他分类项 -->
				<view v-for="(count, type) in typeCount" v-if="type !== 'all'" :key="type" class="category-item"
					:class="{ active: currentType === type }" @click="changeType(type)">
					<text>{{ type }}</text>
					<text class="count">({{ count }})</text>
				</view>
			</view>
		</scroll-view>

		<!-- 商品列表 -->
		<view v-if="loading" class="loading">加载中...</view>
		<view v-else-if="goodsList.length === 0" class="empty">暂无商品</view>

		<scroll-view v-else scroll-y="true" class="scroll-view" @scrolltolower="loadMore">
			<view class="goods-list">
				<view v-for="(item, index) in goodsList" :key="item.id" class="goods-item"
					:class="{'last-item': index === goodsList.length - 1}">
					<!-- 商品图片和信息区域 -->
					<view class="goods-main">
						<!-- 商品图片 - 宽高比3:4 -->
						<view class="image-container">
							<image class="goods-image" :src="item.goods_images[0] || '/static/default-goods.png'"
								mode="aspectFill" />
						</view>

						<!-- 商品信息 -->
						<view class="goods-info">
							<view class="title-row">
								<text class="goods-name">{{ item.name }}</text>
								<view class="tag" v-if="item.is_hot">热销</view>
							</view>

							<view class="goods-details">
								<view class="detail-item">
									<text class="detail-label">尺寸:</text>
									<text class="detail-value">{{ item.size || '无' }}</text>
								</view>
								<view class="detail-item">
									<text class="detail-label">肤色:</text>
									<text class="detail-value">{{ item.skin || '无' }}</text>
								</view>
							</view>

							<!-- 价格显示 -->
							<view class="price-container">
								<text class="goods-price">¥{{ item.total_amount }}</text>
								<text class="goods-currency">{{ item.currency }}</text>
							</view>
						</view>
					</view>

					<!-- 操作按钮 - 单独一行四个按钮 -->
					<view class="action-buttons-single-row">
						<button class="btn preview" @click="previewGoods(item.id)">
							<uni-icons type="eye" size="14" color="#3a3a3a"></uni-icons>
							预览
						</button>
						<button class="btn edit" @click="editGoods(item.id)">
							<uni-icons type="compose" size="14" color="#3a3a3a"></uni-icons>
							编辑
						</button>
						<button class="btn collocation" @click="openCollocation(item.id)">
							<uni-icons type="star" size="14" color="#3a3a3a"></uni-icons>
							搭配参考
						</button>
						<button class="btn sales" @click="openSalesPlan(item.id)">
							<uni-icons type="shop" size="14" color="#3a3a3a"></uni-icons>
							贩售计划
						</button>
					</view>
				</view>
			</view>

			<!-- 加载更多提示 -->
			<view class="load-more">
				<uni-load-more :status="loadStatus" :contentText="{
            contentdown: '上拉加载更多',
            contentrefresh: '正在加载...',
            contentnomore: '没有更多了'
          }" />
			</view>
		</scroll-view>


		<view class="floating-button" @tap="jump2postGoods">
			<uni-icons type="plusempty" size="30" color="#fff"></uni-icons>
		</view>
	</view>
</template>

<script setup>
	import {
		ref,
		onMounted
	} from 'vue';
	import {
		websiteUrl,
		asyncGetUserInfo
	} from "@/common/config.js";

	// 商品列表数据
	const goodsList = ref([]);
	const loading = ref(true);
	const loadStatus = ref('more'); // more / loading / noMore
	const error = ref(null);
	const pageInfo = ref(null);
	const currentPage = ref(1);

	// 新增：分类相关状态
	const typeCount = ref({}); // 存储分类数量 { '全部': 10, '整体': 5, ... }
	const currentType = ref('all'); // 当前选中的分类
	const allCount = ref(0)

	// 获取分类数量统计
	const fetchTypeCount = async () => {
		try {
			const token = uni.getStorageSync('token');
			const res = await uni.request({
				url: `${websiteUrl.value}/brand-manager/get-goods-count`,
				method: 'GET',
				header: {
					Authorization: token
				},
				data: {
					brand_id: 1 // 这里需要替换为实际的品牌ID
				}
			});

			if (res.data.status === 'success') {
				// 计算全部商品的总数
				let total = 0;
				const counts = res.data.data;
				for (const key in counts) {
					total += counts[key];
				}

				// 只存储原始分类数据，不包含all属性
				typeCount.value = counts;
				// 单独存储全部的总数
				allCount.value = total;
			} else {
				uni.showToast({
					title: res.data.msg || '获取分类数量失败',
					icon: 'none'
				});
			}
		} catch (err) {
			console.error('获取分类数量失败:', err);
			uni.showToast({
				title: '获取分类数量失败',
				icon: 'none'
			});
		}
	};

	// 跳转到发布商品
	const jump2postGoods = () => {

	}

	// 切换分类
	const changeType = (type) => {
		currentType.value = type;
		// 重置分页，重新加载商品列表
		currentPage.value = 1;
		goodsList.value = [];
		fetchGoodsList(1);
	};
	// 预览商品
	const previewGoods = (goodsId) => {
		uni.navigateTo({
			url: `/pages/goods/goods?goods_id=${goodsId}`
		});
	};

	// 编辑商品
	const editGoods = (goodsId) => {
		uni.navigateTo({
			url: `/pages/creator_base/goods_editor/goods_editor?goods_id=${goodsId}`
		});
	};

	// 打开官方搭配
	const openCollocation = (goodsId) => {
		uni.navigateTo({
			url: `/pages/creator_base/collocation_list/collocation_list?goods_id=${goodsId}`
		});
	};

	// 打开贩售计划
	const openSalesPlan = (goodsId) => {
		uni.navigateTo({
			url: `/pages/creator_base/sales_plan/sales_plan?goods_id=${goodsId}`
		});
	};
	// 获取商品列表（修改：添加分类参数）
	const fetchGoodsList = async (page = 1) => {
		try {
			// 更新加载状态
			if (page > 1) {
				loadStatus.value = 'loading';
			} else {
				loading.value = true;
			}

			const token = uni.getStorageSync('token');
			if (!token) {
				error.value = '未登录，请先登录';
				loading.value = false;
				loadStatus.value = 'more';
				return;
			}

			const res = await uni.request({
				url: `${websiteUrl.value}/brand-manager/get-goods-list`,
				method: 'GET',
				header: {
					Authorization: token
				},
				data: {
					page: page,
					page_size: 10,
					type: currentType.value === 'all' ? '' : currentType.value // 如果选中的是全部，则传空字符串
				}
			});

			// 处理响应数据
			const responseData = res.data;
			if (responseData.status === 'success') {
				const newGoods = responseData.data.goods_list || [];

				// 更新当前页码
				currentPage.value = page;

				// 如果是第一页，直接赋值
				if (page === 1) {
					goodsList.value = newGoods;
				} else {
					// 加载更多时追加数据
					goodsList.value = [...goodsList.value, ...newGoods];
				}

				pageInfo.value = {
					page_index: responseData.data.page_index,
					page_size: responseData.data.page_size,
					total: responseData.data.total
				};

				// 检查是否还有更多数据
				const totalPages = Math.ceil(pageInfo.value.total / pageInfo.value.page_size);
				loadStatus.value = pageInfo.value.page_index < totalPages ? 'more' : 'noMore';
			} else {
				error.value = responseData.msg || '获取商品列表失败';
				uni.showToast({
					title: error.value,
					icon: 'none'
				});
				loadStatus.value = 'more';
			}
		} catch (err) {
			error.value = '网络请求失败';
			console.error('获取商品列表失败:', err);
			uni.showToast({
				title: '网络请求失败',
				icon: 'none'
			});
			loadStatus.value = 'more';
		} finally {
			loading.value = false;
		}
	};

	// 加载更多
	const loadMore = () => {
		if (loadStatus.value === 'more') {
			fetchGoodsList(currentPage.value + 1);
		}
	};

	// 页面加载时获取数据
	onMounted(async () => {
		uni.setNavigationBarTitle({
			title: "作品管理"
		});

		// 检查登录状态
		const userInfo = await asyncGetUserInfo();
		if (!userInfo) {
			uni.showToast({
				title: '请先登录',
				icon: 'none'
			});
			setTimeout(() => {
				uni.reLaunch({
					url: '/pages/login/login'
				});
			}, 1500);
			return;
		}

		// 先获取分类数量
		await fetchTypeCount();
		// 再获取商品列表
		fetchGoodsList(1);
	});
</script>

<style lang="less" scoped>
	.container {
		padding: 20rpx;
		background: linear-gradient(to bottom, #f8f9fc, #ffffff);
		min-height: 100vh;
	}

	/* 分类导航栏样式 */
	.category-scroll {
		width: 100%;
		white-space: nowrap;
		background: #fff;
		border-bottom: 1px solid #f1f1f1;
		padding: 10rpx 0;
		/* 减少上下内边距 */
		margin-bottom: 20rpx;
		box-shadow: 0 2rpx 10rpx rgba(0, 0, 0, 0.05);
		height: 70rpx;
		/* 固定高度 */
	}

	.category-list {
		display: inline-flex;
		padding: 0 20rpx;
		height: 100%;
		/* 确保高度一致 */
	}

	.category-item {
		display: inline-flex;
		align-items: center;
		padding: 0 25rpx;
		/* 减少上下内边距 */
		margin-right: 20rpx;
		background: #f5f5f7;
		border-radius: 40rpx;
		font-size: 26rpx;
		/* 稍微减小字体 */
		color: #666;
		height: 50rpx;
		/* 固定高度 */
		flex-shrink: 0;
		/* 防止压缩 */

		/* 激活状态 */
		&.active {
			// background: linear-gradient(135deg, #a6e9f7, #1ed1e1);
			// color: #fff;
			
			background-color: #81D8cf;
			color: #fff6e1;
			text {
				color: #fff6e1;
			}

			.count {
				color: #fff6e1;
			}
		}

		.count {
			font-size: 22rpx;
			/* 减小计数字体 */
			color: #999;
			margin-left: 8rpx;
		}
	}


	.loading,
	.empty {
		text-align: center;
		padding: 40rpx;
		color: #999;
		font-size: 28rpx;
	}

	.scroll-view {
		width: 100%;
		height: calc(100vh - 100rpx);
		/* 减去分类导航栏高度 */
	}

	.goods-list {

		.goods-item {
			display: flex;
			flex-direction: column;
			margin-bottom: 30rpx;
			background: #fff;
			border-radius: 24rpx;
			overflow: hidden;
			box-shadow: 0 6rpx 30rpx rgba(0, 0, 100, 0.05);
			transition: all 0.3s ease;
			position: relative;

			&:active {
				// transform: translateY(4rpx);
				box-shadow: 0 4rpx 15rpx rgba(0, 0, 100, 0.03);
			}

			&.last-item {
				margin-bottom: 60rpx;
			}

			/* 商品图片和信息区域 */
			.goods-main {
				display: flex;
				padding: 20rpx;
			}

			.image-container {
				width: 180rpx;
				height: 230rpx;
				position: relative;
				overflow: hidden;
				border-radius: 16rpx;
				background: #f9f9f9;
				overflow: hidden;
				margin-right: 20rpx;
			}

			.goods-image {
				width: 100%;
				height: 100%;
				display: block;
			}

			.goods-info {
				flex: 1;
				display: flex;
				flex-direction: column;

				.title-row {
					display: flex;
					align-items: center;
					margin-bottom: 12rpx;
				}

				.goods-name {
					font-size: 26rpx;
					font-weight: bold;
					color: #333;
					line-height: 1.4;
					flex: 1;
					overflow: hidden;
					text-overflow: ellipsis;
					display: -webkit-box;
					-webkit-line-clamp: 2;
					-webkit-box-orient: vertical;
				}

				.tag {
					background: linear-gradient(to right, #FFD1DC, #FFB6C1);
					color: white;
					font-size: 22rpx;
					padding: 4rpx 12rpx;
					border-radius: 6rpx;
					margin-left: 15rpx;
				}

				.goods-details {
					display: flex;
					flex-direction: column;
					margin-bottom: 18rpx;
					background: #f9fafd;
					border-radius: 12rpx;
					padding: 15rpx;

					.detail-item {
						display: flex;
						margin-bottom: 8rpx;
						font-size: 26rpx;

						&:last-child {
							margin-bottom: 0;
						}
					}

					.detail-label {
						color: #888;
						width: 100rpx;
						flex-shrink: 0;
					}

					.detail-value {
						color: #555;
						font-weight: 500;
					}
				}

				.price-container {
					display: flex;
					align-items: baseline;
					margin-top: auto;

					.goods-price {
						font-size: 36rpx;
						color: #FF9EB5;
						font-weight: bold;
						margin-right: 12rpx;
						font-family: 'Helvetica Neue', sans-serif;
					}

					.goods-currency {
						font-size: 26rpx;
						color: #999;
						transform: translateY(2rpx);
					}
				}
			}

			/* 单行四个按钮样式 - 放在商品卡片底部 */
			.action-buttons-single-row {
				display: flex;
				justify-content: space-between;
				padding: 0 20rpx 20rpx;
				margin-top: 10rpx;
				gap: 15rpx;

				.btn {
					flex: 1;
					height: 70rpx;
					line-height: 70rpx;
					font-size: 22rpx;
					border-radius: 10rpx;
					margin: 0 4rpx;
					padding: 0;
					display: flex;
					align-items: center;
					justify-content: center;
					transition: all 0.2s ease;
					box-shadow: 0 4rpx 10rpx rgba(0, 0, 0, 0.08);
					

					&:active {
						opacity: 0.85;
						transform: scale(0.95);
						box-shadow: 0 2rpx 6rpx rgba(0, 0, 0, 0.05);
					}

					&:after {
						border: none;
					}

					/* 低饱和糖果色按钮 */
					&.preview {
						// background: linear-gradient(135deg, #A8E6CF, #8ED2C8);
						color: #3a3a3a;
					}

					&.edit {
						// background: linear-gradient(135deg, #FFD3B6, #FFAAA5);
						color: #3a3a3a;
					}

					&.collocation {
						// background: linear-gradient(135deg, #B5C6E0, #9FB8E4);
						color: #3a3a3a;
					}

					&.sales {
						// background: linear-gradient(135deg, #FFB7D5, #FF9EC1);
						color: #3a3a3a;
					}

					.uni-icons {
						margin-right: 6rpx;
					}
				}
			}
		}
	}

	.load-more {
		padding: 30rpx 0;
		text-align: center;
		color: #999;
		font-size: 26rpx;
	}

	.floating-button {
		position: fixed;
		right: 40rpx;
		bottom: 120rpx;
		width: 90rpx;
		height: 90rpx;
		background: linear-gradient(135deg, #a6e9f7, #1ed1e1);
		border-radius: 50%;
		display: flex;
		align-items: center;
		justify-content: center;
		box-shadow: 0 6rpx 20rpx rgba(30, 209, 225, 0.4);
		z-index: 99;
		transition: all 0.3s ease;

		&:active {
			transform: scale(0.95);
			box-shadow: 0 4rpx 12rpx rgba(30, 209, 225, 0.3);
		}
	}
</style>